import React from "react";
import ExploreCard from "./ExploreCard";

const Explore = () => {
  return (
    <section className="py-5">
      <div className="container-sm ">
        <h1 className="text-center fw-bold my-5">Explore Our Popular Places</h1>

        <div className="row text-center g-4">
          <ExploreCard city="Phra Nang" country="Thailand" />
          <ExploreCard city="Bali" country="Indonesia"  />
          <ExploreCard city="Cathedral Cove" country="New Zealand" />
        </div>
      </div>
    </section>
  );
};

export default Explore;
