import React from "react";
import img from "../images/dummyImg.jpg";
import { FaChevronRight } from "react-icons/fa";

const ExploreCard = ({ city, country, bgColor }) => {
  return (
    <div className="col-md">
      <div
        className="card shadow"
        style={{
          overflow: "hidden",
          borderRadius: ".7em",
          border: "none",
        }}
      >
        <img src={img} alt="card" className="card-img-top img-fluid" />
        <div className="card-body bg-light text-start pb-1">
          <p className="card-text">{city}</p>

          {/* second */}
          <div
            className="card-content d-flex justify-content-between mt-3 align-items-center "
            role="button"
          >
            <strong className="card-title text-dark explore-more">{country}</strong>

            <FaChevronRight
              className="icon-hover"
              style={{
                color: bgColor ? "#fff" : "#ff5203",
                backgroundColor: bgColor || "#fff",
                borderRadius: "50%",
                fontSize: "25px",
                padding: "2px",
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExploreCard;
