import React from "react";
import img from "../images/dummyImg.jpg";
import icon from "../images/icon1.jpg";

const BestService = () => {
  return (
    <section
      className="my-5 position-relative"
      style={{ backgroundColor: "#fdeae4", minHeight: "380px" }}
    >
      <div className="container-sm py-5">
        <div className="d-md-flex justify-content-between">
          <img
            src={img}
            alt="service"
            className="img-fluid d-none d-md-block rounded"
            style={{
              width: "45%",
              height: "340px",
              objectFit: "contain",
            }}
          />

          <div className="px-md-5 mb-5 mt-md-5">
            <div className="up">
              <h2 className="fw-bold fs-1">
                We always try to give you the best service
              </h2>
              <p className="lead fs-6 py-3">
                We always try to make our customer happy. We Provide all kind of
                facilities. Your satisfaction is our main popularity.
              </p>
            </div>

            <div
              className="down position-absolute bottom-0 p-3"
              style={{ backgroundColor: "#FFF" }}
            >
              <div className="row">
                <div className="col-6 pb-3">
                  <img
                    src={icon}
                    alt="icon"
                    className="img-fluid"
                    style={{ width: "30px" }}
                  />
                  &nbsp; 20+ years of experience
                </div>
                <div className="col-6 pb-3">
                  <img src={icon} alt="icon" style={{ width: "30px" }} />
                  &nbsp; 20+ years of experience
                </div>
                <div className="col-6">
                  <img src={icon} alt="icon" style={{ width: "30px" }} />
                  &nbsp; 20+ years of experience
                </div>
                <div className="col-6">
                  <img src={icon} alt="icon" style={{ width: "30px" }} />
                  &nbsp; 20+ years of experience
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default BestService;
