import React from "react";
import web from "../images/image.jpg";
import { NavLink } from "react-router-dom";
import "../index.css";
import icon3 from "../images/icon3.jpg";

const Home = () => {
  return (
    <>
      <section id="header" className="">
        <div className="container d-lg-flex justify-content-between align-items-center">
          <div className="text-center text-md-start">
            <h1>
              <strong className="brand-name"> Plan Your Trip </strong>
            </h1>
            <h1>
              <strong className="brand-name"> Book with Us </strong>
            </h1>

            <h2 className=" home-text">
              We always try to make our customer Happy. We provide all kind of
              facilities. Your Satisfaction is our main priority.
            </h2>
            <div className="home-text-discover ">
              <NavLink to=" " className="btn-get-started">
                Discover More
              </NavLink>
            </div>

            <div className="itmcenter d-none d-md-block">
              <NavLink exact className="navbar-brand" to="/">
                <img src={icon3} className="img-fluid" alt="grapeslab logo" />
              </NavLink>
            </div>
          </div>

          <div className=" d-md-flex"> <br /><br />
            <img
              src={web}
              className="img-fluid rounded animated"
              alt="grapeslab"
              style={{ objectFit: "cover" }}
            />
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
