import React from "react";
import img from "../images/dummyImg.jpg";
import { AiOutlineHeart } from "react-icons/ai";
import { GoPrimitiveDot } from "react-icons/go";
import { VscLocation } from "react-icons/vsc";
import { NavLink } from "react-router-dom";

const BestPackage = () => {
  return (
    <section className="py-5">
      <div className="container-sm">
        <h1 className="text-center fw-bold my-5">Our Best Packages For You</h1>

        <div className="row row-cols ">
          <Button className="button2"  text="Hot Deals" />

          <Button text="Honeymoon" />
          <Button text="Backpack" />
          <Button text="South Asia" />
          <Button text="Europe" />
          <Button text="More" />
        </div>

        <div className="row text-center g-4 my-4">
          <Card
            country="Switzarland"
            price="$500/Person"
            nights="3 Days, 2 Night"
            title="Explore The Beauty of Lauterbrunnen in Bern"
            iconColor="#ff5203"
          />
          <Card
            country="USA"
            price="$500/Person"
            nights="3 Days, 2 Night"
            title="Explore The Beauty of Beaver Dam"
          />
          <Card
            country="United Kingdom"
            price="$500/Person"
            nights="3 Days, 2 Night"
            title="Explore the beauty of Skye"
            iconColor="lightgreen"
          />
        </div>

        <h6 className="text-center" style={{ margin: "4em 0" }}>
          <a
            href="#"
            className="button2 text-decoration-none text-light px-5 py-3 rounded shadow"
            style={{ backgroundColor: "#ff5203" }}
          >
            Discover More
          </a>
        </h6>
      </div>
    </section>
  );
};

export default BestPackage;
const Button = ({ text, selected }) => {
  return (
    <div className="col">
      <button
        className=" button rounded px-5 py-2 my-1 "
        style={{
          backgroundColor: selected ? "#ff5203" : "f9f9f9",
          border: "unset",
          color: selected ? "#fff" : "000",
          fontSize: "14px",
        }}
      >
        {text}
      </button>
    </div>
  );
};

const Card = ({ nights, price, country, title, iconColor }) => {
  return (
    <div className="col-md col-md-6 col-lg-4 position-relative bs-card">
      <div
        className="card shadow"
        style={{
          overflow: "hidden",
          borderRadius: ".7em",
          border: "none",
        }}
      >
        <AiOutlineHeart
          role="button"
          className="position-absolute top-0 end-0 m-3 heart-icon"
          style={{
            backgroundColor: "#fff",
            borderRadius: "50%",
            fontSize: "30px",
            padding: "4px",
          }}
        />

        <img
          src={img}
          alt="card"
          className="card-img-top img-fluid"
          style={{ height: "340px", objectFit: "cover" }}
        />
        {/* card body */}
        <div className="card-body bg-light text-start pb-0 hover-effect">
          <div
            className="d-flex justify-content-between align-items-center "
            style={{ fontSize: "12px" }}
          >
            <p className="d-flex align-items-center">
              <GoPrimitiveDot
                style={{ color: iconColor || "darkblue", fontSize: "17px" }}
              />
              {nights}
            </p>

            <p className="fw-bold text-secondary">{price}</p>
          </div>
          <div className="card-title text-dark fw-bold">{title}</div>
          <div
            className="d-flex justify-content-between"
            style={{ fontSize: "12px" }}
          >
            <p className="fs-6 d-flex align-items-center text-secondary">
              <VscLocation />
              {country}
            </p>

            <a
              href="#"
              className="fw-bold text-decoration-underline explore-more"
              style={{ color: "#000" }}
            >
              Explore More
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
