import React from "react";
import { BsStarFill } from "react-icons/bs";

import bgImg from "../images/carousel.png";
const Carousel = ({ name, country, img, desc, star, id }) => {
  console.log(country);
  return (
    <div
      className={`carousel-item position-relative ${id === 1 ? "active" : ""}`}
    >
      <img src={bgImg} className="d-block w-100" alt="..." />
      <div
        className="carousel-caption  "
        style={{
          position: "absolute",
          top: "0",
        }}
      >
        <h2 className="title">Our Client's Say About us</h2>
        <p className="desc">{desc}</p>
        <span className="stars">
          {star.map((stars, i) => (
            <BsStarFill
              key={i}
              style={{ color: "gold", marginRight: ".5em" }}
            />
          ))}
        </span>
        <p className="name">{name}</p>
        <p className="country">{country}</p>
        <img
          src={img}
          alt={name}
          className="rounded-circle mb-3 img-thumbnail"
        />
      </div>
    </div>
  );
};

export default Carousel;
